.App-logo {
  height: 30vmin;
}

.App-header {
  display: inline-flex;
  align-items: center;
  font-size: 20px;
  font-family: sans-serif;
  color: #e0454b;
}

.App-body {
  border-top: solid;
  border-bottom: solid;
  border-color: #e0454b;
  min-height: 50vmax;
  padding: 30px;
}
.App-footer {
  display: flex;
  justify-content: center;
  font-style: italic;
  font-family: sans-serif;
}
.email {
  margin-right: 0.5rem;
  font-weight: bold;
}
.password, .label-button {
  margin: 0.5rem;
  font-weight: bold;
}